.search {
  &-result {
    position: absolute;
    z-index: 20;
    display: none;
    min-width: 460px;
    max-height: 400px;
    padding: 20px;
    background-color: #ffffff;
    border: 1px solid var(--light-grey);
    border-radius: 10px;
    box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.302);
    overflow-y: auto;
    transition: 0.25s ease-in-out;

    @media (max-width: 991.98px) {
      min-width: unset;
      max-width: 95%;
    }

    @media (max-width: 719.98px) {
      max-width: 98%;
      width: 100%;
      max-height: calc(100vh - 200px);
    }

    &.fixed {
      z-index: 10;

      &.show {
        z-index: 20;
      }
    }

    &.show {
      z-index: 100;
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    & > p {
      margin: 0;
    }

    & > ul {
      margin: 0;
    }
  }

  &-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &-item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
    gap: 10px;
    font-weight: 400;
    font-size: 15px;
    line-height: 1.25;
    padding: 15px 0;
    border-top: 1px solid var(--light-grey);
    transition: 0.25s ease-in-out;

    @media (max-width: 719.98px) {
      gap: 5px;
      font-size: 11px;
    }

    & > p {
      margin: 0;
    }

    &:hover {
      text-decoration: none !important;

      & .search-item__name {
        color: var(--blue);
      }
    }

    &:first-child {
      border-top: none;
    }

    & img {
      width: 47px;
      height: 47px;
      object-fit: cover;
      aspect-ratio: 1 / 1;
      overflow: hidden;

      @media (max-width: 719.98px) {
        width: 25px;
        height: 25px;
      }
    }

    &__name {
      font-weight: 600;
      color: var(--dark);
      transition: 0.25s ease-in-out;
    }

    &__content {
      max-width: 440px;
      white-space: wrap;
    }

    &__variant {
      font-weight: 500;
      font-size: 12px;
      color: var(--dark-grey);
      transition: 0.25s ease-in-out;

      @media (max-width: 719.98px) {
        font-size: 11px;
      }
    }

    &__price {
      font-weight: 600;
      color: var(--orange);
    }
  }
}
